import { usePropsContext } from "../compontents/StateContext";

const News = () => {
    const {newsData} = usePropsContext();

    const filterNewsData = newsData.filter(item => item.source.name !== "Pausaparafeminices.com" && item.source.name !== "YouTube");
    console.log(filterNewsData)

    return (
        <section className="news">
            <div className="news-container w-3/4 mx-auto my-8">

                <div className="news-wrapper">
                    {filterNewsData.map((item, index) => (
                        <div 
                            key={index} 
                            className="news-item flex flex-col md:flex-row items-start gap-4 p-4 border-b border-gray-200 hover:bg-gray-50 transition">
                            
                            <img 
                                src={item.urlToImage ? item.urlToImage : ''} 
                                alt='' 
                                className="w-full md:w-48 md:h-48 h-auto object-cover rounded-md shadow-md"
                            />
                            
                            <div className="news-content flex flex-col gap-2">
                            <a target="_blank" rel="noopener noreferrer" href={item.url}>
                                <h2 className="text-lg font-semibold text-gray-800 hover:underline">
                                    {item.title}
                                </h2>
                            </a>
                            <p className="text-sm text-gray-600 line-clamp-3">
                                {item.description}
                            </p>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    )
}

export default News;